import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import api from './api.js'
import routes from './routerConfig.js'
import VueLazyload from 'vue-lazyload';
// Vue.use(VueLazyload);
Vue.use(VueLazyload, {
  //   loading: 'http://img1.imgtn.bdimg.com/it/u=3683125983,2937664868&fm=23&gp=0.jpg',
  loading: '../imgs/loading.gif',
  attempt: 1,
})

Vue.use(VueRouter)

/* eslint-disable no-new */
var router = new VueRouter({
  routes
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app');

